const SCREEN_WIDTHS = {
  MOBILE: '667px',
  TABLET: '1024px',
  DESKTOP: '1200px',
};

const mediaQueries = {
  isMobile: `(max-width: ${SCREEN_WIDTHS.MOBILE})`,
  isTablet: `(min-width: ${SCREEN_WIDTHS.MOBILE}) and (max-width: ${SCREEN_WIDTHS.TABLET})`,
  isDesktop: `(min-width: ${SCREEN_WIDTHS.TABLET}) and (max-width: ${SCREEN_WIDTHS.DESKTOP})`,
  isUnderTablet: `(max-width: ${SCREEN_WIDTHS.TABLET})`,
  isUpperTablet: `(min-width: ${SCREEN_WIDTHS.TABLET})`,
  isUnderDesktop: `(max-width: ${SCREEN_WIDTHS.DESKTOP})`,
};

export default mediaQueries;
