import { ApolloClient, ApolloLink, split, InMemoryCache } from '@apollo/client';
import { getMainDefinition } from '@apollo/client/utilities';
import { createUploadLink } from 'apollo-upload-client';
import CONFIG from 'config';
import { WebSocketLink } from '@apollo/client/link/ws';
import authenticationServiceInstance from 'contexts/User/authenticationService';

const wsLink = new WebSocketLink({
  uri: CONFIG.subscriptionUrl,
  options: {
    reconnect: true,
  },
});

const authMiddleware = new ApolloLink((operation, forward) => {
  const token = authenticationServiceInstance.getToken();
  operation.setContext({
    uri: CONFIG.apiUrl,
    headers: {
      authorization: token ? `Bearer ${token}` : '',
    },
  });

  return forward(operation);
});

const uploadLink = createUploadLink();

const link = ApolloLink.from([authMiddleware, uploadLink]);

const splitLink = split(
  ({ query }) => {
    const definition = getMainDefinition(query);
    return (
      definition.kind === 'OperationDefinition' &&
      definition.operation === 'subscription'
    );
  },
  wsLink,
  link
);

const apolloClient = new ApolloClient({
  cache: new InMemoryCache(),
  link: splitLink,
});

export default apolloClient;
