import { useTheme } from '@emotion/react';
import styled from '@emotion/styled';
import IconButton from 'components/Buttons/IconButton';
import React, { createContext, useContext, useEffect, useReducer } from 'react';
import createHash from 'utils/createHash';
import { initialState, reducer } from './reducer';
import PropTypes from 'prop-types';

const computeVariant = (variant, theme) => {
  switch (variant) {
    case 'success':
      return {
        color: theme.successDarken,
        background: theme.successLighten,
        borderColor: theme.success,
      };
    default:
    case 'info':
      return {
        color: theme.white,
        background: theme.secondary,
        borderColor: theme.secondaryDarken,
      };
    case 'error':
      return {
        color: theme.errorDarken,
        background: theme.errorLighten,
        borderColor: theme.error,
      };
    case 'warning':
      return {
        color: theme.warningDarken,
        background: theme.warningLighten,
        borderColor: theme.warning,
      };
  }
};

const SnackbarContainer = styled.div`
  width: 350px;
  position: sticky;
  bottom: 0px;
  margin: auto;
  padding-bottom: ${({ notEmpty }) => (notEmpty ? '16px' : '0px')};
  align-self: center;
  display: flex;
  flex-direction: column-reverse;
  & > *:not(:first-child) {
    margin-bottom: 12px;
  }
  z-index: 100001;
`;

const CustomSnackbar = styled.span`
  padding: 12px;
  border-radius: ${({ theme }) => theme.borderRadius};
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.15);
  border: ${({ theme }) => theme.divider};
  display: flex;
  justify-content: space-between;
  align-items: center;

  border-color: ${({ borderColor }) => borderColor};
  background: ${({ background }) => background};
  color: ${({ color }) => color};

  transition: ${({ theme }) => theme.transition};

  animation-duration: 0.15s;
  animation-name: popup;

  @keyframes popup {
    from {
      /* transform: translateY(100%); */
      /* opacity: 0; */
      transform: scale(0);
    }
    to {
      /* transform: translateY(0%); */
      /* opacity: 1; */
      transform: scale(1);
    }
  }
`;

const Snackbar = ({ id, message, variant, closable, onMount, onClose }) => {
  const theme = useTheme();
  const { color, background, borderColor } = computeVariant(variant, theme);

  useEffect(() => {
    onMount && onMount();
  }, []);

  return (
    <CustomSnackbar
      id={id}
      color={color}
      background={background}
      borderColor={borderColor}
    >
      {message}
      {closable && <IconButton onClick={onClose} size='xxs' icon='close' />}
    </CustomSnackbar>
  );
};

const SnackBarContext = createContext();

const SnackbarProvider = ({ children, options }) => {
  const { Provider } = SnackBarContext;
  const { maxStackSize } = options;

  const [state, dispatch] = useReducer(reducer, {
    ...initialState,
    maxStackSize,
  });

  return (
    <Provider value={{ state, dispatch }}>
      {children}
      <SnackbarContainer notEmpty={state.snackbars.length}>
        {state.snackbars.map((snackbar) => (
          <Snackbar key={snackbar.id} {...snackbar} />
        ))}
      </SnackbarContainer>
    </Provider>
  );
};

const useSnackbar = () => {
  const { _, dispatch } = useContext(SnackBarContext);

  const preparePayload = (message, variant, opts) => {
    const id = `snackbar-${createHash(new Date().toISOString())}`;

    const closeSnackbar = () =>
      dispatch({ type: 'close_snackbar', payload: { id } });

    return {
      type: 'enqueue_snackbar',
      payload: {
        message,
        variant,
        ...opts,
        id,
        onClose: closeSnackbar,
        onMount:
          opts?.delay !== 0
            ? () => setTimeout(closeSnackbar, opts?.delay || 5000)
            : null,
      },
    };
  };

  const methods = {
    success: (message, opts) =>
      dispatch(preparePayload(message, 'success', opts)),
    error: (message, opts) => dispatch(preparePayload(message, 'error', opts)),
    warning: (message, opts) =>
      dispatch(preparePayload(message, 'warning', opts)),
    info: (message, opts) => dispatch(preparePayload(message, 'info', opts)),
  };

  return methods;
};

SnackbarProvider.defaultProps = {
  options: {
    maxStackSize: 3,
  },
};

SnackbarProvider.propTypes = {
  children: PropTypes.func.isRequired,
  options: PropTypes.shape({
    maxStackSize: PropTypes.number,
  }),
};

export { useSnackbar };
export default SnackbarProvider;
