import RequestLoader from 'components/Loader/RequestLoader';
import React, { Suspense } from 'react';

const HomePageRoute = React.lazy(() => import('routes/Home'));
const SigninSignupRoute = React.lazy(() => import('routes/Security'));
const NotFoundRoute = React.lazy(() => import('routes/NotFound'));

const routes = {
  Home: {
    path: '/',
    Component: () => (
      <Suspense fallback={<RequestLoader />}>
        <HomePageRoute />
      </Suspense>
    ),
  },
  Auth: {
    path: '/sign/:state',
    Component: () => (
      <Suspense fallback={''}>
        <SigninSignupRoute />
      </Suspense>
    ),
  },
  notFound: {
    path: '*',
    Component: () => (
      <Suspense fallback={<RequestLoader />}>
        <NotFoundRoute />
      </Suspense>
    ),
  },
};

export default routes;
