const computeSize = (size) => {
  let width = '24px';
  let height = '24px';
  switch (size) {
    case 'xxs':
      width = '8px';
      height = '8px';
      break;
    case 'xs':
      width = '12px';
      height = '12px';
      break;
    case 'sm':
      width = '16px';
      height = '16px';
      break;
    default:
    case 'md':
      break;
    case 'lg':
      width = '32px';
      height = '32px';
      break;
    case 'xl':
      width = '48px';
      height = '48px';
      break;
    case 'xxl':
      width = '64px';
      height = '64px';
      break;
  }

  return { width, height };
};

const computeColor = (color, theme) => {
  return theme[color] || theme.default;
};

export { computeSize, computeColor };
