import React, {
  createContext,
  useCallback,
  useContext,
  useEffect,
  useState,
} from 'react';
import authenticationServiceInstance from './authenticationService';
import { useLazyQuery } from '@apollo/client';
import { CURRENT_USER } from './queries';
import { useHistory } from 'react-router';
import { useSnackbar } from 'contexts/Snackbar';

const UserContext = createContext();

const UserProvider = ({ children }) => {
  const history = useHistory();
  const { Provider } = UserContext;
  const { error: errorToast } = useSnackbar();

  const [isAuthenticated, setIsAuthenticated] = useState(
    authenticationServiceInstance.getIsAuthenticated()
  );
  const [userData, setUserData] = useState();
  const [getCurrentUser, { data, error }] = useLazyQuery(CURRENT_USER, {
    fetchPolicy: 'network-only',
  });
  const [hasValidToken, setHasValidToken] = useState(
    !!authenticationServiceInstance.getToken()
  );

  const connectUser = useCallback((token) => {
    authenticationServiceInstance.connectUser(token);
    setIsAuthenticated(true);
    setHasValidToken(true);
  }, []);

  const disconnectUser = useCallback(() => {
    authenticationServiceInstance.disconnectUser();
    setIsAuthenticated(false);
    setHasValidToken(false);
    setUserData(null);
  }, []);

  useEffect(() => {
    if (isAuthenticated) {
      getCurrentUser();
    }
  }, [isAuthenticated, getCurrentUser]);

  useEffect(() => {
    // Handle the error
    // Go to the login route
    if (error) {
      disconnectUser();
      errorToast(error.message || 'You have been disconnect. Please relogin.', {
        closable: true,
      });
      history.replace(`/sign/in`, {
        from: `${window.location.pathname}${window.location.search}${window.location.hash}`,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [error]);

  const expireUserToken = useCallback(() => {
    authenticationServiceInstance.expireUserToken();
    setHasValidToken(false);
  }, []);

  useEffect(() => {
    setUserData(data?.me);
  }, [data]);

  return (
    <Provider
      value={{
        userData,
        isAuthenticated,
        hasValidToken,
        expireUserToken,
        connectUser,
        disconnectUser,
      }}
    >
      {children}
    </Provider>
  );
};

const useUser = () => {
  const { connectUser, disconnectUser, ...state } = useContext(UserContext);

  const methods = {
    loginSuccess: (token) => {
      connectUser(token.token);
    },
    logout: () => {
      disconnectUser();
    },
  };

  return [state, methods];
};

export { useUser };
export default UserProvider;
