import React from 'react';
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Switch,
} from 'react-router-dom';
import { ApolloProvider } from '@apollo/client';
import apolloClient from 'external-services/apollo-client';
import dayjs from 'dayjs';
import routes from 'routes';
import 'helpers/updateWith';

import { ThemeProvider } from '@emotion/react';
import theme from 'theme';
import CSSProvider from 'components/CSSProvider';
import SnackbarProvider from 'contexts/Snackbar';
import UserProvider, { useUser } from 'contexts/User';

dayjs.locale('fr');

const PrivateRoute = (props) => {
  const [{ isAuthenticated }] = useUser();
  if (isAuthenticated) return <Route {...props} />;

  return <Redirect to={`/sign/in?from=${window.location.pathname}`} />;
};

const App = () => (
  <Router>
    <ThemeProvider theme={theme}>
      <ApolloProvider client={apolloClient}>
        <SnackbarProvider>
          <UserProvider>
            <CSSProvider>
              <Switch>
                <PrivateRoute
                  exact
                  path={routes.Home.path}
                  component={routes.Home.Component}
                />
                <Route
                  exact
                  path={routes.Auth.path}
                  component={routes.Auth.Component}
                />
                <Route
                  path={routes.notFound.path}
                  component={routes.notFound.Component}
                />
              </Switch>
            </CSSProvider>
          </UserProvider>
        </SnackbarProvider>
      </ApolloProvider>
    </ThemeProvider>
  </Router>
);

export default App;
