// eslint-disable-next-line no-extend-native
Array.prototype.updateWith = function (data, baseKey) {
  const res = this.map((currData) => {
    const updatedIndex = data.findIndex(
      (d) => d[baseKey] === currData[baseKey]
    );
    //
    if (updatedIndex >= 0) return data[updatedIndex];

    return currData;
  });
  return res;
};
