const initialState = {
  snackbars: [],
  maxStackSize: 0,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case 'enqueue_snackbar':
      return {
        ...state,
        snackbars: [...state.snackbars, action.payload],
      };
    case 'close_snackbar':
      return {
        ...state,
        snackbars: state.snackbars.filter((s) => s.id !== action.payload.id),
      };
    default:
      return state;
  }
};

export { initialState, reducer };
