import React from 'react';
import Resources from './resources';
import { computeColor, computeSize } from './utils';
import PropTypes from 'prop-types';
import { useTheme } from '@emotion/react';

const Icon = ({ icon, color, size, className }) => {
  const theme = useTheme();
  const hex = computeColor(color, theme);
  const { width, height } = computeSize(size);

  if (!Resources?.[icon]) throw new Error(`Resource ${icon} doesn't exist.`);

  const IconComponent = Resources[icon];

  return (
    <IconComponent
      className={className}
      color={hex}
      width={width}
      height={height}
    />
  );
};

Icon.defaultProps = {
  color: 'default',
  size: 'md',
};

Icon.propTypes = {
  icon: PropTypes.string.isRequired,
  color: PropTypes.oneOf([
    'primary',
    'secondary',
    'default',
    'textPrimary',
    'textSecondary',
  ]),
  size: PropTypes.oneOf(['xs', 'sm', 'md', 'lg', 'xl']),
  children: PropTypes.func.isRequired,
};

export default Icon;
